.frame {
    width: 100%;
    height: 300px;
    overflow-x: hidden;
    -ms-flex: 1;
    display: flex;
    justify-content: space-around;
  }
  .displayHolder{
    width: 100%;
    height: 500px;
    overflow-x: hidden;
    text-align: center;
    padding-left: 23%;
  }
  a {
    text-decoration: none;
  }
  .holder {
    width: 200px;
    height: 250px;
    border: outset;
    margin: 0;
    box-shadow: 5px 5px 2px grey;
    border-radius: 10px;
    overflow: hidden;
  }
  .holder img{
    width: 200px;
    height: 200px;
  }
  .legend {
    width: 100%;
    text-align: center;
  }
  .sub {
    padding-top: 250px;
  }
  @media screen and (max-width: 1100px)  {
      .holder {
        width: 150px;
        height: 200px;
      }
      .holder img{
        width: 150px;
        height: 150px;
      }
  }
  @media screen and (max-width: 800px)  {
    .holder {
      width: 100px;
      height: 150px;
    }
    .holder img{
      width: 100px;
      height: 100px;
    }
}
@media screen and (max-width: 550px)  {
  .frame {
    display: block;
    height: 320px;
    overflow-y: scroll;
  }
  .holder {
    width: 250px;
    height: 300px;
    margin-left: 26%;
    margin-bottom: 20px;
  }
  .holder img{
    width: 250px;
    height: 250px;
  }
}
@media screen and (max-width: 550px)  {
  .holder {
    margin-left: 20%;
  }
}
@media screen and (max-width: 360px)  {
  .holder {
    margin-left: 10%;
  }
}